.gswpts_style-4.gswpts_style-4 table thead th,
.gswpts_style-4.gswpts_style-4 table tbody tr td {
  background-color: #000;
  color: white;
}
.gswpts_style-4.gswpts_style-4 table tr {
  margin-bottom: 10px;
}
.gswpts_style-4.gswpts_style-4 table tr:hover {
  background-color: rgba(255, 255, 255, 0.25);
}
.gswpts_style-4.gswpts_style-4 table a {
  color: orange;
}
.gswpts_style-4.gswpts_style-4 table a:hover {
  color: #2ecc40;
}
.gswpts_style-4.gswpts_style-4 table .sorting.sorting.sorting::after {
  color: #fff;
}
@media screen and (max-width: 768px) {
  .gswpts_style-4.gswpts_style-4.gswpts_responsive .sorting.sorting.sorting::after {
    margin-right: 8px;
  }
}